<template>
    <div>
        <section class="skills standard_container" id="skills">
            <h3 class="section_title">
                Skills
            </h3>

            <div class="skills_information">
                <h5>Design</h5>
                <div class="skills_bar">
                    <span class="skillbar_design" v-bind:class="inViewport.now ? 'design_visible' : 'design_hidden'"><span>90%</span></span>

                </div>
                <h5>CSS3</h5>
                <div class="skills_bar">
                    <span class="css3_design" v-bind:class="inViewport.now ? 'css3_visible' : 'css3_hidden'"><span>85%</span></span>
                </div>
                <h5>JavaScript</h5>
                <div class="skills_bar">
                    <span class="java_design" v-bind:class="inViewport.now ? 'java_visible' : 'java_hidden'"><span>70%</span></span>
                </div>
            </div>





        </section>
    </div>
</template>

<script>
    import inViewport from '../../../node_modules/vue-in-viewport-mixin';
    export default {
        name: "scriptum_skills",
        mixins: [ inViewport ],
        data() {
            return {


            }
        },

        methods: {

        },
        computed: {

        },

        watch: {
           'inViewport.now': function(visible) {
               // console.log('This component is '+( visible ? 'in-viewport' : 'hidden'));
            }
        }

    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";

    .skills {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-left: 20px;
        padding-right: 20px;
        box-sizing: border-box;



        &>.skills_information {
            max-width: 1270px;
            width: 100%;
            box-sizing: border-box;


            &>h5 {
                color: $h_black_color;
                font-family: 'baron_neueregular';
                margin-bottom: 20px;

            }

            &>.skills_bar {
                width: 100%;
                height: 35px;
                background-color: $bg_color;
                margin-bottom: 35px;

                &>.skillbar_design, .css3_design, .java_design {
                    background-color: $link_black;
                    height: 35px;
                    width: 0;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    transition: all 1.5s ease;
                    overflow: hidden;

                    &>span {
                        padding-right: 10px;
                        box-sizing: border-box;
                        color: $h_white_color;
                        font-family: 'baron_neueregular';
                        font-size: 12px;
                    }

                }
                &>.skillbar_design {
                    transition-delay: 0.3s;
                }
                &>.css3_design {
                    transition-delay: 0.5s;
                }
                &>.java_design {
                    transition-delay: 0.7s;
                }
                &>.design_visible {
                    width: 95%;
                }
                &>.design_hidden {
                    width: 0;
                    transition: all 0.1s ease;
                    transition-delay: 0s;
                }

                &>.css3_visible {
                    width: 85%;
                }
                &>.css3_hidden {
                    width: 0;
                    transition: all 0.1s ease;
                    transition-delay: 0s;
                }

                &>.java_visible {
                    width: 70%;
                }
                &>.java_hidden {
                    width: 0;
                    transition: all 0.1s ease;
                    transition-delay: 0s;
                }
            }



        }
    }

</style>


