<template>
    <div>
        <section class="works standard_container" id="works">

            <div class="works_block">
                <h3 class="section_title">
                    Works
                </h3>

                <ul class="works_sort_list">
                    <li v-for="(list, index) in categories">

                        <a href="" @click.prevent="current_category=list.category" v-if="list.category!=current_category">{{list.category}}</a>
                        <span v-else>{{list.category}}</span>
                    </li>

                </ul>

                <div class="works_list">
                    <transition-group name="photo_list-complete">

       <img v-for="(item, index) in SortWork"
            :key="item.name"

            :src="'/static/works/thumbs/'+item.thumb"
            @click="showLightbox(item.name)"
            class="works_photos_links">


                    </transition-group>

                    <lightbox id="mylightbox"
                              ref="lightbox"
                              :images="images"
                              :directory="thumbnailDir"
                              :timeoutDuration="5000"
                              :filter="current_category"
                    ></lightbox>

                </div>

            </div>

        </section>
    </div>
</template>

<script>
    export default {
        name: "scriptum_works",
        data() {
            return {
                thumbnailDir: '/static/works/',
                images: [
                    { name: 'work_art_1.jpg', thumb:'work_art_1_thumb.jpg', filter:'art' },
                    { name: 'work_art_2.jpg', thumb:'work_art_2_thumb.jpg', filter:'art' },
                    { name: 'work_art_3.jpg', thumb:'work_art_3_thumb.jpg', filter:'art' },
                    { name: 'work_art_4.jpg', thumb:'work_art_4_thumb.jpg', filter:'art' },
                    { name: 'work_art_5.jpg', thumb:'work_art_5_thumb.jpg', filter:'art' },
                    { name: 'work_art_6.jpg', thumb:'work_art_6_thumb.jpg', filter:'art' },
                    { name: 'work_art_7.jpg', thumb:'work_art_7_thumb.jpg', filter:'art' },
                    { name: 'work_brand_1.jpg', thumb:'work_brand_1_thumb.jpg', filter:'brand' },
                    { name: 'work_brand_2.jpg', thumb:'work_brand_2_thumb.jpg', filter:'brand' },
                    { name: 'work_brand_3.jpg', thumb:'work_brand_3_thumb.jpg', filter:'brand' },
                    { name: 'work_brand_4.jpg', thumb:'work_brand_4_thumb.jpg', filter:'brand' },
                    { name: 'work_design_1.jpg', thumb:'work_design_1_thumb.jpg', filter:'design' },
                    { name: 'work_design_2.jpg', thumb:'work_design_2_thumb.jpg', filter:'design' },
                    { name: 'work_design_3.jpg', thumb:'work_design_3_thumb.jpg', filter:'design' },
                    { name: 'work_web_1.jpg', thumb:'work_web_1_thumb.jpg', filter:'web' },
                    { name: 'work_web_2.jpg', thumb:'work_web_2_thumb.jpg', filter:'web' },
                    { name: 'work_web_3.jpg', thumb:'work_web_3_thumb.jpg', filter:'web' },
                    { name: 'work_web_4.jpg', thumb:'work_web_4_thumb.jpg', filter:'web' },
                    { name: 'work_web_5.jpg', thumb:'work_web_5_thumb.jpg', filter:'web' }

                ],
                categories: [
                    {category: "all"},
                    {category: "design"},
                    {category: "web"},
                    {category: "brand"},
                    {category: "art"}

                ],
                current_category: 'all'
            }
        },
        computed: {
            SortWork () {
                //console.log(this.current_category);
                let current = this.current_category;
                return this.images.sort(function(){return 0.5 - Math.random()}).filter(function (u) {
                        if(current=='all') { return u.filter }
                        else {
                            return u.filter==current

                        }
                })
            }
        },
        methods: {
            showLightbox(imageName) {
                this.$refs.lightbox.show(imageName);
            }
        }
        }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";
.works {
    .works_block {
        margin: 0 auto;
        max-width: 1270px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        &>h3 {
            text-align: center;
        }

        &>.works_sort_list {
            list-style: none;
            display: flex;
            flex-direction: row;
            margin: 0;
            padding: 0;

            &>li {
                margin-right: 25px;
                font-family: 'baron_neueregular', sans-serif;
                //text-transform: uppercase;
                font-size: 13px;

                &:last-child {
                    margin-right: 0;
                }

                &>a {
                    text-decoration: none;
                    color: $link_black;
                    cursor: pointer;
                    transition: all 0.3s ease;

                    &:hover {
                        color: $link_grey;

                    }
                }

                &>span {
                    color: $link_grey;
                }

            }


        }


    }
}

    .works_list {



        &>span {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: center;
            flex-flow: wrap;
            transition: all 1s ease;

        }

    }
    .works_photos_links {
        width: 300px;
        transition: all 1s ease;
        display: inline-block;
        margin: 10px;
    }


</style>


