<template>
    <div>
        <section class="contacts standard_container" id="contacts">

            <h3 class="section_title">
                Contact us
            </h3>

            <div class="contacts_form">
                <input type="text" v-model="firstname" placeholder="*NAME" class="contacts_form_input form_grid_one"
                       :class="!firstname ? '' : !firstname_val ? 'non-valid': ''">
                <input type="text" v-model="surname" placeholder="*SURNAME" class="contacts_form_input form_grid_one"
                       :class="!surname ? '' : !surname_val ? 'non-valid': ''">
                <input type="text" v-model="email" placeholder="*EMAIL" class="contacts_form_input form_grid_two"
                       :class="!email ? '' : !email_val ? 'non-valid': ''">
                <textarea name="" v-model="mes" id="" cols="30" rows="10" placeholder="*MESSAGE" class="contacts_form_textarea form_grid_two">

                </textarea>
                <button class="contacts_form_button form_grid_one"  :disabled="!button_val">SEND</button>
            </div>


        </section>
    </div>
</template>

<script>
    export default {
        name: "scriptum_contacts",
        data() {
            return {
                firstname: null,
                surname: null,
                email: null,
                mes: null,

                firstname_val: false,
                surname_val: false,
                email_val: false,
                mes_val: false,

                button_val: false
            }
        },
        computed: {
            checkBtn: function() {
                if (this.firstname_val && this.surname_val && this.email_val && this.mes_val) {
                    this.button_val = true;
                }
                else {
                    this.button_val = false
                }
            }
        },
        watch: {
            firstname: function (firstname) {
                var namecheck = /^[a-zA-Z ]+$/;
                this.firstname_val = namecheck.test(firstname);
                this.checkBtn;

            },
            surname: function (surname) {
                var namecheck = /^[a-zA-Z ]+$/;
                this.surname_val = namecheck.test(surname);
                this.checkBtn;

            },

            email: function(email) {
                var validmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                this.email_val = validmail.test(email);
                this.checkBtn;
            },
            mes: function(mes) {
                mes ? this.mes_val=true : this.mes_val = false;
                this.checkBtn;
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";
    .contacts {

        &>h3 {
            margin-bottom: 50px;
        }

        &>.contacts_form {
            display: grid;
            grid-template-rows: auto auto;
            grid-gap: 20px;
            max-width: 1004px;
            margin: 0 auto;
            padding: 0 20px;
            box-sizing: border-box;

            @media screen and (max-width: $smallest) {

            }

            &>.contacts_form_input {

                background-color: $form_bg_color;
                color: $h_black_color;
                font-family: 'baron_neueregular';
                text-transform: lowercase;
                border: none;
                outline: none;
                padding: 15px;
                transition: all 0.5s ease;

                &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
                    color: $form_text_inside;
                    opacity: 1; /* Firefox */
                }
            }

            &>.contacts_form_textarea {
                background-color: $form_bg_color;
                color: $h_black_color;
                font-family: 'baron_neueregular';
                text-transform: lowercase;
                border: none;
                outline: none;
                padding: 15px;
                margin: 0;

                &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
                    color: $form_text_inside;
                    opacity: 1; /* Firefox */
                }
            }

            &>.contacts_form_button {
                font-family: 'baron_neueregular';
                text-transform: lowercase;
                background: none;
                //border-radius: 10px;
                padding: 10px 30px;
                cursor: pointer;
                border:1px solid rgba(235,235,235,1);
                box-shadow: 0px 5px 25px -10px rgba(0,0,0,0.5);
                transform: translate(0px, -3px);
                transition: all 0.5s ease;
                outline: none;
                justify-self: start;


                &:hover {

                    border:1px solid rgba(235,235,235,1);
                    box-shadow: 0px 5px 25px -10px rgba(0,0,0,0.8);
                    transform: translate(0px, -6px);
                }
                &:disabled {
                    box-shadow: 0px 5px 25px -10px rgba(0,0,0,0);
                    transform: translateY(0px);
                    border:1px solid rgba(235,235,235,0);
                    cursor: auto;
                }

            }

            &>.form_grid_one {
                @media screen and (max-width: $smallest) {
                    grid-column: 1 / 3;
                }

            }
            &>.form_grid_two {
                grid-column: 1 / 3;
            }


        }
    }

    .non-valid {
        box-shadow: 0px 0px 7px 1px rgba(255,0,0,0.75);
        transition: all 0.5s ease;
    }

</style>


