import Vue from 'vue'
import Router from 'vue-router'
import Scriptum_index from '@/components/Scriptum_index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Scriptum_index',
      component: Scriptum_index
    }
  ]
})



