<template>
    <div>
        <header class="header" id="home">
            <div class="logo">
                <div class="logo_square logo_square_element">
                    <img src="../assets/images/s_logo.png" alt="">
                </div>
                <div class="logo_square"></div>
            </div>
            <div class="menu">

               <!-- <div class="menu_square">

                </div>-->
                <div class="menu_square menu_square_element" @click="display=!display">
                    <img src="../assets/images/menu_burger.png" alt="">
                </div>
                <div class="menu_list" :class="display ? 'opened' : 'closed'">
                    <span class="close_button" @click="display=!display"><i class="fas fa-times"></i></span>
                    <scrollactive class="my-nav"
                                  active-class="active"
                                  :duration="1000">
                    <ul class="menu_list_items">
                        <li><a href="#home" class="scrollactive-item">Home</a></li>
                        <li><a href="#about" class="scrollactive-item">About</a></li>
                        <li><a href="#works" class="scrollactive-item">Works</a></li>
                        <li><a href="#skills" class="scrollactive-item">Skills</a></li>
                        <li><a href="#news" class="scrollactive-item">News</a></li>
                        <li><a href="#contacts" class="scrollactive-item">Contact us</a></li>

                    </ul>
                    </scrollactive>
                </div>

            </div>
        </header>
        <scriptum_about />
        <scriptum_works />
        <scriptum_development />
        <scriptum_skills />
        <scriptum_iphone />
        <scriptum_news />
        <scriptum_contacts />
        <scriptum_footer />
    </div>
</template>

<script>
    import scriptum_about from '../components/blocks/scriptum_about'
    import scriptum_works from '../components/blocks/scriptum_works'
    import scriptum_development from '../components/blocks/scriptum_development'
    import scriptum_skills from '../components/blocks/scriptum_skills'
    import scriptum_iphone from '../components/blocks/scriptum_iphone'
    import scriptum_news from '../components/blocks/scriptum_news'
    import scriptum_contacts from '../components/blocks/scriptum_contacts'
    import scriptum_footer from '../components/blocks/scriptum_footer'
    export default {
        name: "scriptum_index",
        data () {
            return {
                display: false
            }
        },
        components: {
            scriptum_about,
            scriptum_works,
            scriptum_development,
            scriptum_skills,
            scriptum_iphone,
            scriptum_news,
            scriptum_contacts,
            scriptum_footer
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/css/config";

.header {
    background: url("../assets/images/scriptum_title_background.jpg") no-repeat center/cover;
    width: 100%;
    height: 65vh;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width:$smallest) {
        height: 50vh;
    }

    &>.logo {
        position: relative;

        &>.logo_square {
            position: absolute;
            top: 20px;
            left: 20px;
            border: 2px solid $h_white_color;
            width: 50px;
            height: 80px;
            transition: all 0.5s ease;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &>.logo_square_element {
            position: absolute;
            top: 15px;
            left: 15px;

            &>img {
                transition: all 0.5s ease;
            }
        }

        &:hover {

            &>.logo_square {
                transform: translate(3px, 3px);
                transition: all 0.5s ease;
            }
            &>.logo_square_element {
                background-color: $h_white_color;
                transform: translate(-3px, -3px);

                &>img {
                    filter:invert(100%);
                    transition: all 0.5s ease;
                }

            }
        }



    }

    &>.menu {
        position: fixed;
        top: 20px;
        right: 20px;
        z-index: 99;

        @media screen and (max-width:$smallest) {
            top: 3px;
            right: 3px;
        }

        &>.menu_square {
            position: absolute;
            //top: 20px;
            //right: 20px;
            border: 2px solid $h_grey_color;
            background-color: $h_white_color;
            width: 50px;
            height: 80px;
            transition: all 0.5s ease;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &>.menu_square_element {
            position: absolute;
            top: 15px;
            right: 15px;
            cursor: pointer;

            &>img {
                transition: all 0.5s ease;
            }
        }

        &:hover {

            &>.menu_square {
                //background-color: rgba(235,235,235,0);
                //transform: translate(2px, -3px);
                transition: all 0.5s ease;
            }
            &>.menu_square_element {
               //transform: translate(-3px, 2px);

                &>img {
                    filter:invert(50%);
                    transition: all 0.5s ease;
                }

            }
        }

        &>.menu_list {
            position: absolute;
            top: 10px;
            right: 10px;


            &>.close_button {
                width: 25px;
                height: 25px;
                cursor:pointer;
                position: absolute;
                top:0;
                left: 1px;
                z-index: 5;
                background-color: $h_grey_color;
                text-align: center;
                box-sizing: border-box;
                padding-top: 4px;
            }
            &>nav {
            &>.menu_list_items {
                position: relative;
                list-style: none;
                background-color: $h_white_color;
                margin: 0;
                padding: 0;
                width: 150px;
                box-sizing: content-box;
                outline: 3px solid $h_grey_color;
                outline-offset: 3px;



                &>li {


                    border-bottom: 1px solid $h_grey_color;
                    padding: 5px 10px;
                    text-align: center;
                    cursor: pointer;
                    transition: all 0.5s ease;

                    &:hover {
                        background-color: $h_grey_color;
                    }

                    &>a {
                        text-decoration: none;
                        font-family: 'baron_neueregular', sans-serif;
                        font-weight: normal;
                        font-size: 16px;
                        color: $link_black;
                        transition: all 0.5s ease;
                    }

                    &>a.active {
                        color: #900101;
                    }
                }



            }
            }
        }



    }


}

    .opened {
        opacity: 1;
        transition: all 0.5s ease;
        height: 250px;
        width: 155px;
        padding-top: 5px;
        padding-left: 30px;
        overflow: hidden;
    }
    .closed {
        transition: all 0.5s ease;
        height: 0;
        overflow: hidden;
        width: 155px;
        padding-top: 0;
        padding-left: 30px;


    }

</style>


