<template>
    <div>
        <section class="standard_container development">
            <div class="development_block">
                <div class="development_title">
                    <div class="development_title_square_element">
                        Web Development
                    </div>
                    <div class="development_title_square">

                    </div>
                </div>
            </div>
            <div class="development_details_block">
                <ul class="development_items">
                    <li class="development_items_content">
                        <img src="../../assets/images/Design_icon.png" alt="Design">
                        <h6>Design</h6>
                    </li>
                    <li class="development_items_content">
                        <img src="../../assets/images/Lamp_icon.png" alt="Night work">
                        <h6>Night work</h6>
                    </li>
                    <li class="development_items_content">
                        <img src="../../assets/images/Sent_icon.png" alt="Communication">
                        <h6>Communication</h6>
                    </li>
                    <li class="development_items_content">
                        <img src="../../assets/images/Password_icon.png" alt="Safety">
                        <h6>Safety</h6>
                    </li>
                    <li class="development_items_content">
                        <img src="../../assets/images/Compass_icon.png" alt="Coordination">
                        <h6>Coordination</h6>
                    </li>
                    <li class="development_items_content">
                        <img src="../../assets/images/Engineering_icon.png" alt="Development">
                        <h6>Development</h6>
                    </li>
                </ul>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "scriptum_development"
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";
.development {

    padding: 0;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;

    &>.development_block {

        background: url("../../assets/images/development_bg.jpg") no-repeat center/cover;
        height: 500px;
        flex: 1 0 50%;
        display: flex;

        justify-content: center;
        align-items: center;


        &>.development_title {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: 85%;
            min-width: 60%;


            &>.development_title_square {

                border: 2px solid $h_white_color;
                position: absolute;
                width: 100%;
                height: 100%;
                transition: all 0.5s ease;
                display: inline-block;
                justify-content: center;
                align-items: center;
                transform: translate(3px, 3px);
                margin: 0;
                padding: -2px;
                box-sizing: border-box;

            }
            &>.development_title_square_element {
                width: 100%;
                height: 100%;

                position: relative;
                border: 2px solid $h_white_color;
                font-family: 'baron_neueregular';
                font-size: 30px;
                color: $h_white_color;
                padding: 10px 20px;
                text-align: center;
                box-sizing: border-box;
                transition: all 0.5s ease;
                
                display: inline;
                transform: translate(-3px, -3px);


            }


            &:hover {

                &>.development_title_square {
                    transform: translate(6px, 6px);
                    transition: all 0.5s ease;

                    }
                &>.development_title_square_element {
                    background-color: $h_white_color;
                    transform: translate(-6px, -6px);
                    transition: all 0.5s ease;
                    color: $h_black_color;

                }

            }



        }



    }
    &>.development_details_block {
        height: 500px;
        flex: 1 0 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        @media screen and (max-width: 740px) {
            height: 350px;
        }

        &>.development_items {

            list-style: none;
            width: 100%;
            display: grid;
            padding: 0 50px;
            grid-template-columns: repeat(3, minmax(100px, 1fr));

            @media screen and (max-width: 1100px) {
                grid-template-columns: repeat(2, minmax(100px, 1fr));
            }
            @media screen and (max-width: 740px) {
                grid-template-columns: repeat(2, minmax(100px, 1fr));
            }

            &>.development_items_content {

                padding: 30px 20px;
                box-sizing: border-box;
                text-align: center;
                transition: all 0.3s ease;
                @media screen and (max-width: 1100px) {
                    padding: 20px 10px;
                }
                @media screen and (max-width: $smallest) {
                    padding: 10px 5px;
                }

                &>img {
                    width: 60px;
                    margin-bottom: 10px;

                    @media screen and (max-width: 1100px) {
                        width: 50px;
                    }
                    @media screen and (max-width: $smallest) {
                        width: 30px;
                    }
                }
                &>h6 {
                    transition: all 0.3s ease;
                    font-family: 'baron_neueregular', sans-serif;
                }

                &:hover {
                    transform: scale(1.05, 1.05);

                    &>h6 {
                        color: #585959;
                    }
                }
            }
        }
    }
}
</style>


