<template>
  <div>
      <section class="about standard_container" id="about">
        <div class="about_block">
            <img src="../../assets/images/picture_about.jpg" alt="">
            <div class="about_text">
                <h2 class="section_title">About Scriptum</h2>
                <p>It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors.</p>
                <a href="" class="learnMore_link">Learn more</a>
            </div>
        </div>
      </section>
  </div>
</template>

<script>
    export default {
        name: "scriptum_about"
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/config";
.about_block {
    margin: 0 auto;
    max-width: 1270px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media screen and (max-width: $small) {
        flex-direction: column;
    }

    &>img {
        width: 50%;

        @media screen and (max-width: $small) {
            width: 75%;
        }
    }

    &>.about_text {
        width: 50%;
        text-align: left;
        padding: 0 15px;
        box-sizing: border-box;

        @media screen and (max-width: $small) {
            width: 75%;
            text-align: center;
        }

        &>h2 {
            text-align: left;
            margin-bottom: 20px;

            @media screen and (max-width: $small) {
                text-align: center;
            }

        }

        &>p {
            font-family: 'Work Sans', sans-serif;
            font-size: 14px;
            line-height: 22px;
            //letter-spacing: 1px;
            color: $text_color;
            margin-bottom: 50px;


        }
    }

}

</style>


